.contentBox{
    display: flex;
    flex-direction: row;
    gap: 10%;
    margin-top: 3%;
}
.contentBox>div{
  width: 20%;
  max-height: 15rem;
}
.contentBox div h3{
    font-size: 16px;
    font-weight: 800;
    font-family: Montserrat;
    color: #3a3a3a;
    padding-bottom: 15px;
    position: relative;
}
.contentBox div h3::after{
    content: "";
    position: absolute;
    bottom: 1%;
    top: 16px;
    background: red;
    width: 45%;
    height: 6%;
    left: 0;
    
}

.contentBox h1::before{
    width: 31%;
}
.contentBox>div p{
    color: #3a3a3a;
    font-size: 12px;
    line-height: 18px;
    overflow: hidden;
    font-weight: 500;
}
.expend{
    height: 100%;
}
.readBtn{
    color: red;
    font-size: 12px;
    line-height: 18px;
}
.shortText{
    height: 67%;
}