.slider{
    position: relative;
    width: 100%;
    height: 100%;
}
.slides{
    
    position: absolute;
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
}
.btnContainer{
    position: absolute;
    width: 100%;
    top: 40%;
    height: 25%;
}
.back-image{
    background-position: center;
    background-size: cover;
    width: 4%;
    height: 89%;
}